/**
 * 文本转语音工具
 * 使用浏览器内置的 Web Speech API
 */

export class TextToSpeech {
    private static instance: TextToSpeech
    private synth: SpeechSynthesis | null = null
    private voices: SpeechSynthesisVoice[] = []
    private isInitialized = false

    private constructor() {
        if (typeof window !== 'undefined') {
            this.synth = window.speechSynthesis
            this.loadVoices()
        }
    }

    static getInstance(): TextToSpeech {
        if (!TextToSpeech.instance) {
            TextToSpeech.instance = new TextToSpeech()
        }
        return TextToSpeech.instance
    }

    /**
     * 加载可用的语音列表
     */
    private loadVoices() {
        if (!this.synth) return

        const loadVoicesCallback = () => {
            this.voices = this.synth!.getVoices()
            this.isInitialized = true
        }

        // Chrome 需要等待 voiceschanged 事件
        if (this.synth.onvoiceschanged !== undefined) {
            this.synth.onvoiceschanged = loadVoicesCallback
        }

        // 立即尝试加载一次
        loadVoicesCallback()
    }

    /**
     * 获取英语语音
     * 优先选择美式英语，其次英式英语
     */
    private getEnglishVoice(): SpeechSynthesisVoice | null {
        if (!this.isInitialized || this.voices.length === 0) {
            return null
        }

        // 优先美式英语
        const usVoice = this.voices.find(
            (voice) =>
                voice.lang === 'en-US' ||
                voice.lang.startsWith('en-US') ||
                voice.name.toLowerCase().includes('us') ||
                voice.name.toLowerCase().includes('american')
        )

        if (usVoice) return usVoice

        // 其次英式英语
        const ukVoice = this.voices.find(
            (voice) =>
                voice.lang === 'en-GB' ||
                voice.lang.startsWith('en-GB') ||
                voice.name.toLowerCase().includes('uk') ||
                voice.name.toLowerCase().includes('british')
        )

        if (ukVoice) return ukVoice

        // 最后任何英语语音
        const anyEnglishVoice = this.voices.find((voice) =>
            voice.lang.startsWith('en')
        )

        return anyEnglishVoice || null
    }

    /**
     * 朗读文本
     * @param text 要朗读的文本
     * @param options 朗读选项
     */
    speak(
        text: string,
        options: {
            rate?: number // 语速 (0.1 - 10, 默认 1)
            pitch?: number // 音调 (0 - 2, 默认 1)
            volume?: number // 音量 (0 - 1, 默认 1)
            lang?: string // 语言 (默认 en-US)
            onEnd?: () => void // 朗读结束回调
            onError?: (error: SpeechSynthesisErrorEvent) => void // 错误回调
        } = {}
    ): void {
        if (!this.synth) {
            console.warn('Speech synthesis not supported in this browser')
            return
        }

        // 停止当前正在播放的语音
        this.stop()

        const utterance = new SpeechSynthesisUtterance(text)

        // 设置语音
        const voice = this.getEnglishVoice()
        if (voice) {
            utterance.voice = voice
        }

        // 设置参数
        utterance.rate = options.rate ?? 0.9 // 稍微慢一点，便于学习
        utterance.pitch = options.pitch ?? 1
        utterance.volume = options.volume ?? 1
        utterance.lang = options.lang ?? 'en-US'

        // 设置回调
        if (options.onEnd) {
            utterance.onend = options.onEnd
        }

        if (options.onError) {
            utterance.onerror = options.onError
        }

        // 开始朗读
        this.synth.speak(utterance)
    }

    /**
     * 朗读单词（专门为单词学习优化）
     * @param word 单词
     * @param options 选项
     */
    speakWord(
        word: string,
        options: {
            slow?: boolean // 是否慢速朗读
            repeat?: number // 重复次数（默认1次）
            onComplete?: () => void // 全部完成回调
        } = {}
    ): void {
        const { slow = false, repeat = 1, onComplete } = options
        let count = 0

        const speakOnce = () => {
            count++
            this.speak(word, {
                rate: slow ? 0.6 : 0.9,
                onEnd: () => {
                    if (count < repeat) {
                        // 重复朗读，间隔500ms
                        setTimeout(speakOnce, 500)
                    } else if (onComplete) {
                        onComplete()
                    }
                },
            })
        }

        speakOnce()
    }

    /**
     * 朗读例句
     * @param sentence 例句
     * @param options 选项
     */
    speakSentence(
        sentence: string,
        options: {
            rate?: number
            onEnd?: () => void
        } = {}
    ): void {
        this.speak(sentence, {
            rate: options.rate ?? 0.85, // 例句稍微慢一点
            onEnd: options.onEnd,
        })
    }

    /**
     * 停止朗读
     */
    stop(): void {
        if (this.synth) {
            this.synth.cancel()
        }
    }

    /**
     * 暂停朗读
     */
    pause(): void {
        if (this.synth) {
            this.synth.pause()
        }
    }

    /**
     * 恢复朗读
     */
    resume(): void {
        if (this.synth) {
            this.synth.resume()
        }
    }

    /**
     * 检查是否正在朗读
     */
    isSpeaking(): boolean {
        return this.synth?.speaking ?? false
    }

    /**
     * 获取可用的语音列表
     */
    getAvailableVoices(): SpeechSynthesisVoice[] {
        return this.voices
    }
}

// 导出单例实例
export const tts = typeof window !== 'undefined' ? TextToSpeech.getInstance() : null

// 便捷函数
export const speakWord = (word: string, options?: Parameters<TextToSpeech['speakWord']>[1]) => {
    tts?.speakWord(word, options)
}

export const speakSentence = (sentence: string, options?: Parameters<TextToSpeech['speakSentence']>[1]) => {
    tts?.speakSentence(sentence, options)
}

export const stopSpeaking = () => {
    tts?.stop()
}
